.header {
    margin-top: 100px;
  
    .header_container {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      align-items: center; /* Center vertically */
    }
  
    .profile-image-container {
      border: 3px solid $theme-color; /* Add a solid black border */
      border-radius: 50%; /* Make the image circular */
      overflow: hidden; /* Hide any overflow outside the circle */
      width: 225px;
      height: 233px; /* Adjust the dimensions for a square shape */
    }
  
    img.profile-image {
      width: 100%;
      height: 100%; /* Make the image cover the circular container */
    }
  
    h1 {
      font-weight: 600;
      font-size: 60px;
      margin: 0;
    }
  
    h3 {
      font-weight: 400;
      margin-top: 2px;
      color: $theme-color;
      font-size: 35px;
    }

  
    @media screen and (max-width: 599px) {
      .profile-image-container {
        width: 135px;
        height: 135px;
      }

      h1 {
        font-weight: 600;
        font-size: 23px;
        margin: 0;
      }

      h3 {
        font-size: 15px;
      }
    }

    @media screen and (min-width: 600px) {
      .profile-image-container {
        width: 150px;
        height: 155px;
      }

      h1 {
        font-weight: 600;
        font-size: 28px;
        margin: 0;
      }

      h3 {
        font-size: 19px;
      }
    }

    @media screen and (min-width: 768px) {
      .profile-image-container {
        width: 170px;
        height: 175px;
      }
    
      h1 {
        font-weight: 600;
        font-size: 45px;
        margin: 0;
      }
    
      h3 {
        font-size: 26px;
      }
    }

    @media only screen and (min-width: 992px) {
      .profile-image-container {
        width: 200px;
        height: 205px;
      }
    
      h1 {
        font-weight: 600;
        font-size: 50px;
        margin: 0;
      }
    
      h3 {
        font-size: 30px;
      }
    }
  }
  